<template>
	<aside  class="widget widget_tag_cloud">
        <h3 class="widget-title"><span>标签</span></h3>
        <div v-if="tagList.length == 0" class="aligncenter"><img src="/assets/images/loading-dian.gif" alt=""></div>
        <div class="tagcloud" >
            <a v-for="tag in tagList" class="pjax" :href="'/land/list-0-0-0-'+tag.tag_name+'/'" :title='tag.tag_name' style='font-size: 13px;'>{{tag.tag_name}}</a>
        </div>
        <div class="clearfix"></div>
    </aside>
</template>


<script>
import Vue from "vue"
export default {
  data () {
    return {
       url : "http://llmztt.com/land/TagList/",
       tagList:[]
    }
  },
  mounted: function() {
      this.getTagList() 
  },
  created: function() {
      
  },
  methods:{
    getTagList:function(){
        var _this = this   
        Vue.http.get(this.url )
        .then(function(response) {
            var data = response.data
            _this.tagList = data
        }, function(response) {
            console.log('fail ' + response.status + "," + response.statusText)
        })
        .catch(function(response) {
            console.log(response)
        })
    }
      
  } 

}
</script>
 
<style scoped>
 
</style>


