<template>
	<aside class="widget cs_widget_comment">
        <h3 class="widget-title"><span>最新评论</span></h3>
        <div v-if="commiteList.length == 0" class="aligncenter"><img src="/assets/images/loading-dian.gif" alt=""></div>
        <div class="textwidget" id="comment-list">
            <ul>
                <li v-for="c in commiteList">
                    <img src="/assets/images/User-5S.png" class="avatar" height="64" width="64"> 
                    <p class="s_r">
                        <a class="pjax simptip-smooth simptip-movable simptip-position-top" :href="'/land/view/'+c.commite_article_id+'/'" :data-tooltip="c.commite_uname + ':' + c.commite_content">
                            <span class="s_name">{{c.commite_uname}}:</span>
                            <span class="s_desc">{{c.commite_content}}</span>
                        </a>
                    </p>
                </li>
            </ul>
        </div>
        <div class="clearfix"></div>
    </aside>  
</template>


<script>
import Vue from "vue" 

export default {
  data () {
    return {
       url : "http://llmztt.com/land/CommiteList/",
       commiteList:[] 
    }
  },
  mounted: function() {
      this.getCommiteList() 
  },
  created: function() {
      
  },
  methods:{
    getCommiteList:function(){
        var _this = this   
        Vue.http.get(this.url )
        .then(function(response) {
            var data = response.data
            _this.commiteList = data
        }, function(response) {
            console.log('fail ' + response.status + "," + response.statusText)
        })
        .catch(function(response) {
            console.log(response)
        })
    }
      
  } 

}
</script>
 
<style scoped>
 
</style>


