<template>
    <aside class="widget widget_blogstat">
        <h3 class="widget-title">
        <span>博客统计</span></h3> 
        <div v-if="!wb.article_count" class="aligncenter"><img src="/assets/images/loading-dian.gif" alt=""></div> 
        <ul>
            <li>日志总数：{{wb.article_count}} 篇</li>
            <li>评论数目：{{wb.commite_count}} 条</li>
            <li>建站日期：{{wb.web_time}}</li>
            <li>运行天数：{{wb.web_daycount}} 天</li>
            <li>标签总数：{{wb.tag_count}} 个</li>
            <li>最后更新：{{wb.last_updatetime}}</li>
            </ul>
        <div class="clearfix"></div>
    </aside>
</template>

<script>
import Vue from "vue"
export default {
  data () {
    return {
       url : "http://llmztt.com/land/WebBase/",
       wb:{}
    }
  },
  mounted: function() {
      this.getBaseInfo() 
  },
  created: function() {
      
  },
  methods:{
    getBaseInfo:function(){
        var _this = this   
        Vue.http.get(this.url )
        .then(function(response) {
            var data = response.data
            _this.wb = data 
        }, function(response) {
            console.log('fail ' + response.status + "," + response.statusText)
        })
        .catch(function(response) {
            console.log(response)
        })
    }
      
  } 

}
</script>
 
<style scoped>
 
</style>


