<template>
  <div class="box">
      <!--header-->
      <web-header />
      <!--nav-->
      <web-nav />
      <!--banner-->
      <web-banner />
      <!--container-->
      <web-container :type-list="typeList" /> 
      <!-- footer -->
      <web-footer :type-list="typeList" />
  </div>
</template>

<script>
import Vue          from 'vue' 
import WebHeader    from 'components/include/Header' 
import WebNav       from 'components/include/Nav'
import WebBanner    from 'components/include/Banner'
import WebContainer from 'components/Container' 
import WebFooter    from 'components/include/Footer' 
import configs      from 'assets/common' 
import Func         from 'assets/Func' 
 

export default {
  
  data: function() {
    return { 
       dataUrl : configs.webPath,
       typeList:[],
       params:{method:"typeList"}
    }
  },
  mounted: function() {
    var _this = this
    Func.getViewWebData(this.dataUrl,this.params).then(function(data){
        _this.typeList = data
    })

  },
  computed: function() {
      
  },
  methods:{
    
  },
  components: { WebHeader,WebNav,WebBanner,WebFooter,WebContainer}
}  
</script>

<style>

</style>



