<template>
	<aside class="widget widget_archive">
      <h3 class="widget-title"><span>文章归档</span></h3>  
      <div v-if="archivesList.length == 0" class="aligncenter"><img src="/assets/images/loading-dian.gif" alt=""></div>
      <ul> 
        <li v-for="arc in archivesList"><a class="pjax" :href="'/land/list-0-0-'+arc.year+'.'+arc.month+'-0/'">{{arc.year}}年{{arc.month}}月</a>&nbsp;({{arc.count}})</li>
      </ul>
      <div class="clearfix"></div>
  </aside>
</template>

<script>
import Vue from "vue"
export default {
  data () {
    return {
       url : "http://llmztt.com/land/GetArchives/",
       archivesList:[]
    }
  },
  mounted: function() {
      this.getArchivesList() 
  },
  created: function() {
      
  },
  methods:{
    getArchivesList:function(){
        var _this = this   
        Vue.http.get(this.url )
        .then(function(response) {
            var data = response.data
            _this.archivesList = data
        }, function(response) {
            console.log('fail ' + response.status + "," + response.statusText)
        })
        .catch(function(response) {
            console.log(response)
        })
    }
      
  } 

}
</script>
 
<style scoped>
 
</style>


